import React from "react";
import ReactDOM from "react-dom";
import games from "./data";
import Card from "./Card";
import SearchBar from "./SearchBar";

console.log(games);

function App() {
  return (
    <div>
      <SearchBar />
      {games.map((gameItem) => (
        <Card
          gameThumbnail={gameItem.gameThumbnail}
          gameName={gameItem.gameName}
        />
      ))}
      ;
    </div>
  );
}

export default App;
